	module.exports = {"data":{"allMarkdownRemark":{"totalCount":6,"edges":[{"node":{"fields":{"slug":"/rip-leonard-nimoy"},"excerpt":"","timeToRead":1,"frontmatter":{"title":"RIP Leonard Nimoy","tags":["quote post","The Simpsons","Star Trek","Leonard Nimoy"],"cover":"https://unsplash.it/1152/300/?random?StarTrek","date":"2015-02-28T00:25"}}},{"node":{"fields":{"slug":"/star-wars-posters"},"excerpt":"Love these Star Wars posters  Source  Flickr","timeToRead":1,"frontmatter":{"title":"Star Wars Posters","tags":["quote post","Star Wars"],"cover":"https://unsplash.it/1152/300/?random?StarWars","date":"2015-01-16T16:59"}}},{"node":{"fields":{"slug":"/overcasts-2014-sales-numbers-marcoorg"},"excerpt":"Overcast’s 2014 sales numbers – Marco.org Very positive post from Marco Arment about the revenue generated by his latest app Overcast…","timeToRead":1,"frontmatter":{"title":"Overcast's 2014 sales numbers - Marco.org","tags":["quote post"],"cover":"https://unsplash.it/1152/300/?random?Sales","date":"2015-01-16T08:27"}}},{"node":{"fields":{"slug":"/5-ways-to-burn-out-programming"},"excerpt":"I've just read a great blog post by  Josh Braegger . I highly recommend giving it a read. 5 Ways To Burn Out Programming 1. Think about your…","timeToRead":1,"frontmatter":{"title":"5 Ways To Burn Out Programming","tags":["advice","burn out","quote post"],"cover":"https://unsplash.it/1152/300/?random?BurnOut","date":"2014-03-29T10:19"}}},{"node":{"fields":{"slug":"/design-advice"},"excerpt":"Source  goodfuckingdesignadvice.com","timeToRead":1,"frontmatter":{"title":"Good Fucking Design Advice","tags":["design","quotes","quote post"],"cover":"https://unsplash.it/1152/300/?random?Design","date":"2014-01-25T12:43"}}},{"node":{"fields":{"slug":"/we-give-a-f-how-the-site-loads"},"excerpt":"Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS. What was the issue…","timeToRead":1,"frontmatter":{"title":"We Give a F*** How the Site Loads","tags":["code","quote post"],"cover":"https://unsplash.it/1152/300/?random?FuckCss","date":"2013-08-13T22:57"}}}]},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"tag":"quote post","nodes":[{"node":{"frontmatter":{"title":"RIP Leonard Nimoy","tags":["quote post","The Simpsons","Star Trek","Leonard Nimoy"],"cover":"https://unsplash.it/1152/300/?random?StarTrek","date":"2015-02-28T00:25","author":"eddie"},"fields":{"slug":"/rip-leonard-nimoy"},"html":"<p><img src=\"/images/rip-nimoy-1.png\" alt=\"Hello. I&#x27;m Leonard Nimoy.\"></p>\n<p><img src=\"/images/rip-nimoy-2.png\" alt=\"The following tale of alien encounters is true.\"></p>\n<p><img src=\"/images/rip-nimoy-3.png\" alt=\"And by true, I mean false.\"></p>\n<p><img src=\"/images/rip-nimoy-4.png\" alt=\"It&#x27;s all lies. But they&#x27;re entertaining lies.\"></p>\n<p><img src=\"/images/rip-nimoy-5.png\" alt=\"And in the end, isn&#x27;t that the real truth?\"></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Star Wars Posters","tags":["quote post","Star Wars"],"cover":"https://unsplash.it/1152/300/?random?StarWars","date":"2015-01-16T16:59","author":"eddie"},"fields":{"slug":"/star-wars-posters"},"html":"<p>Love these Star Wars posters</p>\n<p><img src=\"/images/star-wars-poster-bwing.jpg\" alt=\"B-Wing Fighter\"></p>\n<p><img src=\"/images/star-wars-poster-ywing.jpg\" alt=\"Y-Wing Fighter\"></p>\n<p><img src=\"/images/star-wars-poster-tie.jpg\" alt=\"Tie Fighter\"></p>\n<p><img src=\"/images/star-wars-poster-slave.jpg\" alt=\"Slave 1\"></p>\n<p><img src=\"/images/star-wars-poster-falcon.jpg\" alt=\"Millennium Falcon\"></p>\n<p><img src=\"/images/star-wars-poster-snowspeeder.jpg\" alt=\"Snowspeeder\"></p>\n<p><img src=\"/images/star-wars-poster-xwing.jpg\" alt=\"X-Wing Fighter\"></p>\n<p><small> Source <a href=\"https://www.flickr.com/photos/avanaut/sets/72157622290035954\">Flickr</a></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Overcast's 2014 sales numbers - Marco.org","tags":["quote post"],"cover":"https://unsplash.it/1152/300/?random?Sales","date":"2015-01-16T08:27","author":"eddie"},"fields":{"slug":"/overcasts-2014-sales-numbers-marcoorg"},"html":"<p><strong><a href=\"http://www.marco.org/2015/01/15/overcast-sales-numbers\">Overcast’s 2014 sales numbers – Marco.org</a></strong></p>\n<p>Very positive post from Marco Arment about the revenue generated by his latest app Overcast. Particularly this part of the last paragraph.</p>\n<blockquote>\n<p>I'm probably coming in under what I could get at a good full-time job in the city, but I don't have to actually work for someone else on something I don't care about. I can work in my nice home office, drink my fussy coffee, take a nap after lunch if I want to, and be present for my family as my kid grows up. That's my definition of success.</p>\n</blockquote>\n<p><small>Source <strong><a href=\"http://www.marco.org/2015/01/15/overcast-sales-numbers\">marco.org</a></strong></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"5 Ways To Burn Out Programming","tags":["advice","burn out","quote post"],"cover":"https://unsplash.it/1152/300/?random?BurnOut","date":"2014-03-29T10:19","author":"eddie"},"fields":{"slug":"/5-ways-to-burn-out-programming"},"html":"<p>I've just read a great blog post by <a href=\"http://blog.braegger.pw\">Josh Braegger</a>. I highly recommend giving it a read.</p>\n<p><strong><a href=\"http://blog.braegger.pw/5-ways-to-burn-out-programming\">5 Ways To Burn Out Programming</a></strong></p>\n<p><strong>1. Think about your project and only the project</strong></p>\n<blockquote>\n<p>...You put off fun features for the sake of hitting a deadline...</p>\n</blockquote>\n<blockquote>\n<p>...you started programming because you thought it was fun, why not keep programming because it's fun? Take that little extra time to put in a feature you want...</p>\n</blockquote>\n<p><strong>2. Have a negative attitude toward everything</strong></p>\n<blockquote>\n<p>...the best projects I've worked on worked out that way because we had a great, positive team. We enjoyed showing up every day to work, told each other when we did awesome things, held back heavy-handed criticism and phrased it in a productive manner.</p>\n</blockquote>\n<p><strong>3. Use the tools you know, because you're faster that way</strong></p>\n<blockquote>\n<p>...you should prototype, play around, and become an expert in new tech...</p>\n</blockquote>\n<p><strong>4. Switch jobs often</strong></p>\n<blockquote>\n<p>...Your career stagnates, you don't develop your skills as deeply (only breadth), people don't trust you'll stay employed for a while, and you're constantly having to prove yourself...</p>\n</blockquote>\n<p><strong>5. Work long hours, ignore your life</strong></p>\n<blockquote>\n<p>...you go die-hard to meet an impossible deadline. You delivered the project on time, with all the extra features you wanted. You are the hero. High fives all around. And if you're lucky, you'll get that bonus.</p>\n</blockquote>\n<blockquote>\n<p>That's great the first time. But how about the second. And the third. It's a bomb, and you dont know how short the fuse is.</p>\n</blockquote>\n<p><small>Source <a href=\"http://blog.braegger.pw/5-ways-to-burn-out-programming\">5 Ways To Burn Out Programming</a></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Good Fucking Design Advice","tags":["design","quotes","quote post"],"cover":"https://unsplash.it/1152/300/?random?Design","date":"2014-01-25T12:43","author":"eddie"},"fields":{"slug":"/design-advice"},"html":"<p><img src=\"/images/design-advice-work-harder.jpg\" alt=\"Work fucking harder.\"></p>\n<p><img src=\"/images/design-advice-love-your-job.jpg\" alt=\"Love your fucking job.\"></p>\n<p><img src=\"/images/design-advice-do-it-right.jpg\" alt=\"Do it right the first fucking time.\"></p>\n<p><img src=\"/images/design-advice-criticism.jpg\" alt=\"Learn to take some fucking criticism.\"></p>\n<p><img src=\"/images/design-advice-challange-yourself.jpg\" alt=\"Constantly fucking challenge yourself.\"></p>\n<p><img src=\"/images/design-advice-helvetica.jpg\" alt=\"Use fucking Helvetica.\"></p>\n<p><img src=\"/images/design-advice-get-over-yourself.jpg\" alt=\"Get over your fucking self.\"></p>\n<p><img src=\"/images/design-advice-promote.jpg\" alt=\"Promote your fucking self.\"></p>\n<p><img src=\"/images/design-advice-give-a-damn.jpg\" alt=\"Give a fucking damn.\"></p>\n<p><img src=\"/images/design-advice-never-assume.jpg\" alt=\"Never assume fucking anything.\"></p>\n<p><small>Source <a href=\"http://goodfuckingdesignadvice.com\">goodfuckingdesignadvice.com</a></small></p>","timeToRead":1}}],"page":1,"pages":2,"total":6,"limit":5,"next":"/tags/quote-post/2"}}

